<template>
  <div class="controls">
    <GameResults />
    <div class="controls_players">
      <div
        v-for="(player) in game.players"
        class="controls_player"
        :class="{'controls_player-active': player.id === game.player.id}"
        :key="player.id"
      >
        <div class="controls_player_color" :class="['controls_player_color-' + player.id]">{{player.size}}</div>
        <div class="controls_player_reserve">(+{{player.reserve}})</div>
      </div>
    </div>
    <div class="controls_actions">
      <v-btn color="accent" :disabled="game.players.length < 2 || game.busy" @click="nextPlayer">Next turn</v-btn>
      <v-btn color="accent" :disabled="game.busy" outlined @click="newGame">New game</v-btn>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import GameService from '@/services/game.service'
import GameResults from './GameResults.vue'

export default {
  name: 'GameControls',
  components: {
    GameResults
  },
  computed: mapState({
    game: state => state.game,
    gameResults: state => state.game.results
  }),
  data: () => ({
    gameService: new GameService(),
    resultsActive: false
  }),
  methods: {
    nextPlayer () {
      this.gameService.nextPlayer()
    },
    newGame () {
      this.gameService.startGame()
    }
  },
  watch: {
    gameResults () {
      this.resultsActive = true
    }
  }
}
</script>

